var that;
export default {
	data() {
		return {
			data: { title: '提示', content: '这是一个模态弹窗', cancelText: 'cancel', confirmColor: '#3CC51F' },
			type: 'notify',
			show: false,
			phone: '',
			info:{}
		};
	},
	methods: {
		onConfirm(e) {
			console.log(e);
		},
		cancel(e) {
			console.log(e);
		},
		click(e) {
			this.tabMask.show(300);
		},
		getinput(e){
			let value=e.currentTarget.dataset.value;
			let name=e.currentTarget.dataset.name;
			that.info[name]=value;
		}
	},
	onLoad() {
		that=this; 
	}
};
